@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;200;300;400;500;600;700&display=swap');

* {
  color: #fff;
  font-family: 'Roboto Mono', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.app {
  background-color: #343541;
  display: flex;
}

.side-bar {
  background-color: #202123;
  height: 100vh;
  width: 244px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  border: solid 0.5px rgba(255,255,255, 0.5);
  background-color: transparent;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
}

nav {
  border-top: solid 0.5px rgba(255,255,255, 0.5);
  padding: 10px;
  margin: 10px;
}

.history {
  padding: 10px;
  margin: 10px;
  height: 100%;
}

.history li {
  list-style-type: none;
  padding: 15px 0;
  cursor: pointer;
}

.main {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.info {
  color: rgba(255,255,255, 0.5);
  font-size: 11px;
  padding: 10px;
}

.bottom-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input-container {
  position: relative;
  width: 100%;
  max-width: 650px;
}

input {
  width: 100%;
  font-size: 20px;
  border: none;
  background-color: rgba(255,255,255, 0.05);
  padding: 12px 15px;
  border-radius: 5px;
  box-shadow: rgba(0,0,0, 0.05) 0 54px 55px,
                rgba(0,0,0, 0.05) 0 -12px 30px,
                rgba(0,0,0, 0.05) 0 4px 6px,
                rgba(0,0,0, 0.05) 0 12px 13px,
                rgba(0,0,0, 0.05) 0 -3px 5px;
}

input:focus {
  outline: none;
}

#submit {
  position: absolute;
  right: 0;
  bottom: 15px;
  cursor: pointer;

}

.feed {
  overflow: hidden;
  width: 100%;
  padding: 0;
}

.feed li {
  display : flex;
  background-color: #444654;
  width: 100%;
  padding: 20px;
  margin: 20px 0;
}

.feed p {
  color: rgba(255,255,255, 0.8);
  font-size: 14px;
  text-align: left;
  padding: 0 20px;
}

.feed p.role {
  min-width: 100px
}